html, h2, p {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  width: 100vw;
  height: 100vh;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 100px auto;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  margin-inline: auto;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rewiew {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rewiew-count {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}

.stars--rating--1 > :nth-child(-n+1), .stars--rating--2 > :nth-child(-n+2), .stars--rating--3 > :nth-child(-n+3), .stars--rating--4 > :nth-child(-n+4), .stars--rating--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
